<template>
    <div>
        <div ref="sideNav" class="bm-menu">
            <nav class="bm-item-list">
                <slot></slot>
            </nav>
            <span class="bm-cross-button cross-style" @click="closeMenu" :class="{ hidden: !crossIcon }">
                <span v-for="(x, index) in 2" :key="x" class="bm-cross" :style="{ position: 'absolute', width: '3px', height: '14px',transform: index === 1 ? 'rotate(45deg)' : 'rotate(-45deg)'}">
                </span>
            </span>
        </div>

        <div ref="bmBurgerButton" class="bm-burger-button" @click="openMenu" :class="{ hidden: !burgerIcon }">
            <span class="bm-burger-bars line-style" :style="{top:20 * (index * 2) + '%'}" v-for="(x, index) in 3" :key="index"></span>
        </div>
    </div>
</template>

<script>
export default {
    name: 'menubar',
    data() {
        return {
            isSideBarOpen: false
        };
    },
    props: {
        isOpen: {
            type: Boolean,
            required: false
        },
        right: {
            type: Boolean,
            required: false
        },
        width: {
            type: [String],
            required: false,
            default: '300'
        },
        disableEsc: {
            type: Boolean,
            required: false
        },
        noOverlay: {
            type: Boolean,
            required: false
        },
        onStateChange: {
            type: Function,
            required: false
        },
        burgerIcon: {
            type: Boolean,
            required: false,
            default: true
        },
        crossIcon: {
            type: Boolean,
            required: false,
            default: true
        },
        disableOutsideClick: {
            type: Boolean,
            required: false,
            default: false
        }
    },
    methods: {
        openMenu() {
            this.$emit('openMenu');
            this.isSideBarOpen = true;
            /*
            // not over body
            if (!this.noOverlay) {
                document.body.className += 'bm-overlay';
            }
            */
            if (this.right) {
                this.$refs.sideNav.style.left = 'auto';
                this.$refs.sideNav.style.right = '0px';
            }
            this.$nextTick(function() {
            this.$refs.sideNav.style.width = this.width
                ? this.width + 'px'
                : '400px';
            });
        },

        closeMenu() {
            this.$emit('closeMenu');
            this.isSideBarOpen = false;
            /*
            // not over body
            document.body.className = document.body.className.replace(
            'bm-overlay',
            ''
            );
            */
            this.$refs.sideNav.style.width = '0px';
        },

        closeMenuOnEsc(e) {
            e = e || window.event;
            if (e.key === 'Escape' || e.keyCode === 27) {
                this.$refs.sideNav.style.width = '0px';
                document.body.style.backgroundColor = 'inherit';
                this.isSideBarOpen = false;
            }
        },

        documentClick(e) {
            let element = this.$refs.bmBurgerButton;
            
            let target = null;
            if (e && e.target) {
                target = e.target;
            }

            if (
                element &&
                element !== target &&
                !element.contains(target) &&
                e.target.className !== 'bm-menu' &&
                this.isSideBarOpen &&
                !this.disableOutsideClick
            ) {  
                console.log(element)
                console.log(target)
                console.log(e)
                this.closeMenu();
            }
        },
    },

    mounted() {
        if (!this.disableEsc) {
            document.addEventListener('keyup', this.closeMenuOnEsc);
        }
    },
    created: function() {
        // document.addEventListener('click', this.documentClick);
    },
    destroyed: function() {
        document.removeEventListener('keyup', this.closeMenuOnEsc);
        // document.removeEventListener('click', this.documentClick);
    },
    watch: {
        isOpen: {
            deep: true,
            immediate: true,
            handler(newValue, oldValue) {
                this.$nextTick(() => {
                    if (!oldValue && newValue) {
                        this.openMenu();
                    }
                    if (oldValue && !newValue) {
                        this.closeMenu();
                    }
                });
            }
        },
        right: {
            deep: true,
            immediate: true,
            handler(oldValue, newValue) {
                if (oldValue) {
                    this.$nextTick(() => {
                        this.$refs.bmBurgerButton.style.left = 'auto';
                        this.$refs.bmBurgerButton.style.right = '36px';
                        this.$refs.sideNav.style.left = 'auto';
                        this.$refs.sideNav.style.right = '0px';
                        document.querySelector('.bm-burger-button').style.left = 'auto';
                        document.querySelector('.bm-burger-button').style.right = '36px';
                        document.querySelector('.bm-menu').style.left = 'auto';
                        document.querySelector('.bm-menu').style.right = '0px';
                        document.querySelector('.cross-style').style.right='250px';
                    });
                }
                if (newValue) {
                    if (
                        this.$refs.bmBurgerButton.hasAttribute('style')
                    ) {
                        this.$refs.bmBurgerButton.removeAttribute('style');
                        this.$refs.sideNav.style.right = 'auto';
                        document
                        .querySelector('.bm-burger-button')
                        .removeAttribute('style');
                        document.getElementById('sideNav').style.right = 'auto';
                        document.querySelector('.cross-style').style.right='0px';
                    }
                }
            }
        }
    }
};
</script>

<style lang="less">
html {
    height: 100%;
}
.bm-burger-button {
    position: absolute;
    width: 36px;
    height: 30px;
    left: 0px;
    top: 100px;
    cursor: pointer;
}
.bm-burger-button.hidden {
    display: none;
}
.bm-burger-bars {
    background-color: #373a47;
}
.line-style {
    position: absolute;
    height: 20%;
    left: 0;
    right: 0;
}
.cross-style {
    position: absolute;
    top: 12px;
    right: 2px;
    cursor: pointer;
}
.bm-cross {
    background: #bdc3c7;
}
.bm-cross-button {
    height: 24px;
    width: 24px;
}
.bm-cross-button.hidden {
    display: none;
}
.bm-menu {
    height: 100%; /* 100% Full-height */
    width: 0; /* 0 width - change this with JavaScript */ //--------------------current
    position: fixed; /* Stay in place */
    z-index: 5; /* Stay on top */
    top: 0;
    left: 0;
    /* background-color: rgb(245, 245, 245); */
    background-color: white;
    overflow-x: hidden; /* Disable horizontal scroll */
    padding-top: 40px; /* Place content 60px from the top */
    transition: 0.5s; /*0.5 second transition effect to slide in the sidenav*/
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
}

.bm-overlay {
    background: rgba(0, 0, 0, 0.3);
}
.bm-item-list {
    color: #b8b7ad;
    margin: 0px 15px 5px 15px;
    font-size: 20px;

    > * {
        display: flex;
        text-decoration: none;
        padding: 0.7em;

        > span {
            margin-left: 10px;
            font-weight: 700;
            color: black;
        }
    }
}
</style>

